export default mounted;
function mounted() {
  this.$nextTick(() => {
    let _this = this;
    this.$util.getUser((res)=>{
      let i;
      if(res.code === 0){
        for(i=0;i<res.data.length;i++){
          this.menuList.push({
          	account:res.data[i].account,
          	username:res.data[i].username
          });
        }
      }else{
        this.$message({
          type:'error',
          message:'获取用户列表失败'
        })
      }
    })
    this.$util.getRecordNum(null,0,(res)=>{
      if(res.code===0){
        this.recordCount = res.data.recordCount;
        this.pageCount = res.data.pageCount;
        if(this.recordCount != 0){
          this.$util.getOnesPro(null, 1, (res) => {
            if (res.code === 0) {
              this.tableData = res.data;
            }
          })
        }
      }else{
        this.$message({
          type:'error',
          message:'获取最新项目列表失败'
        })
      }
    })
  })
}



