<template>
  <div class="myprojectList">
    <h2>我的项目</h2>
    <el-row>
      <el-col :span="3">&nbsp;</el-col>
      <el-col :span="18">
        <el-table
          :data="tableData"
          border
          style="width:100%"
          max-height="250">
          <el-table-column
            prop="fileName"
            label="项目名称">
          </el-table-column>
          <el-table-column
            prop="href"
            label="项目地址">
          </el-table-column>
          <el-table-column
            label="操作">
            <template scope="scope">
              <el-button
                @click.native.prevent="deleteRow(scope.$index, tableData)"
                type="text"
                size="small">
                移除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="changePage"
          :current-page="currentPage"
          :page-size="10"
          layout="total, prev, pager, next, jumper"
          :total="recordCount"
          :page-count="pageCount">
        </el-pagination>
      </el-col>
      <el-col :span="3">&nbsp;</el-col>
    </el-row>
  </div>
</template>

<script>
export default {
    data() {
      return {
        currentPage:1,
        recordCount:0,
        pageCount:0,
        tableData:[]
      };
    },
    mounted(){
      this.$nextTick(()=>{
          if(!this.isLogin){
            this.$router.push({path:'/'});
          }
          this.$util.getRecordNum(this.userInfo.account,1,(res)=>{
            if(res.code === 0){
              this.recordCount = res.data.recordCount;
              this.pageCount = res.data.pageCount;
            }
          })
      })
    },
    watch:{
      recordCount:{
        handler(val,oldVal){
          this.$util.getOnesPro(this.userInfo.account,1,(res)=>{
            // console.log(res)
            if(res.code === 0){
              this.tableData = res.data;
            }
          })
        },
        deep:true
      }
    },
    methods: {
      deleteRow(index,data){
        // console.log(index,data)
        this.$util.deletePro(this.userInfo.account,data[index].id,(res)=>{
          if(res.code === 0){
            data.splice(index,1);
            this.$message({
              type:'success',
              message:'删除成功'
            })
          }
        })
        
      },
      changePage(val){
        // console.log('当前是：' + val);
        this.$util.getOnesPro(this.userInfo.account,val,(res)=>{
          if(res.code === 0){
            this.tableData = res.data;
          }
        })
      }
    },
    computed:{
        isLogin(){
            return this.$store.state.isLogin;
        },
        userInfo(){
            return this.$store.state.userInfo;
        }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-tree{
  border:0;
  border-right: 1px solid #d1dbe5;
}
.myprojectList h2{
  text-align: center;
}
.el-pagination{
  margin-top: 10px;
  text-align: center;
}
</style>



