export default {
    isInvalid,
    login,
    upload,
    getUser,
    getOnesPro,
    deletePro,
    getRecordNum
}
/**
 * 检测用户输入的数据是否合法
 * @param {Int} type 1-检查账号是否合法; 2-检查密码是否合法
 * @param {String} str 
 * @return {Boolean}
 */
function isInvalid(type,str){
    let tType = parseInt(type,10);
    let tStr = String(str);
    if(isNaN(type)){
        console.log({code:1,msg:"参数1数据类型错误，请使用整型Int"});
        return false;
    }
    switch(type){
        case 0:
            return isAccount(str);
        case 1:
            return isPasswd(str);
        case 2:
            return isCompress(str);
        case 3:
            return isFileName(str);
        case 4:
            return isSize(str);
        default:
            console.log({code:2,msg:"不存在对应的检测类型"});
            return false;
    }
    /**
     * 检测账号是否合法
     * @param {String} str 
     */
    function isAccount(str){
        let reg = /^\d{9,10}$/;
        return reg.test(str);
    }
    /**
     * 检测密码是否合法
     * @param {String} str 
     */
    function isPasswd(str){
        let reg = /[^\w\d]+/;
        return !reg.test(str);
    }
    /**
     * 检测文件后缀名是否为zip
     * @param {*String} str 
     */
    function isCompress(str){
        let reg = /\.(zip|rar)$/;
        return reg.test(str);
    }
    /**
     * 检测项目文件名称是否正确
     * @param {*String} str 
     */
    function isFileName(str){
        let reg = /\w+/;
        return reg.test(str);
    }
    function isSize(str){
        let size = 5 * 1024 *1024;
        let fileSize = parseInt(str, 10);
        if(fileSize && fileSize < size){
            return true;
        }
        return false;
    }
}

/**
 * 对应接口文档第一个接口，登录接口
 * @param {*String} account 
 * @param {*String} password 
 * @param {*Function} callback 
 * 
 * 请求方式: POST
 * 请求参数: account-账号,password-密码
 * 返回参数: code-状态码,0代表成功
 *          msg-状态说明
 */
function login(account,password,callback){
    $snrj.ajax({
        url:LOGIN,
        data:{
            account:account,
            password:password
        },
        method:'post',
        dataType:'json',
        success:function(status,res,e){
            if(status.status === 200){
                callback(res);
            }else{
                callback({code:2,msg:status.status + ' : ' + status.statusText});
            }
        }
    })
}

/**
 * 上传接口，对应接口文档第二个接口
 * @param {*String} fileName 
 * @param {*object File} file 
 * @param {*Function} callback 
 * 
 * 请求方式: POST
 * 请求参数: fileName-项目名称，file-项目文件压缩流
 * 返回参数: code-状态码,0代表成功
 *          msg-状态说明信息
 */
function upload(fileName,file,progress,callback){
    $snrj.ajax({
        url:UPLOAD,
        data:{
            filename:fileName,
            file:file
        },
        method:'post',
        dataType:'json',
        success:function(status,res,e){
            if(status.status === 200){
                callback(res);
            }else{
                callback({code:2,msg:status.status + ' : ' + status.statusText});
            }
        },
        progress:function(loaded,total,data){
            progress(loaded,total,data);
        }
    })
}

/**
 * 查询所有账号，对应接口三
 * @param {*Function} callback
 * 
 * 请求方式: GET
 * 请求参数: 无
 * 返回参数: code-状态码,0代表成功
 *          msg-状态说明
 *          data-返回的数据项，list，包含以下参数
 *          account-账户 
 */
function getUser(callback){
    $snrj.ajax({
        url:SEARCH,
        method:'get',
        dataType:'json',
        success:function(status,res,e){
            if(status.status === 200){
                callback(res);
            }else{
                callback({code:2,msg:status.status + ' : ' + status.statusText});
            }
        }
    })
}

/**
 * 获取某一用户的所有项目，对应接口四
 * @param {*String} account 
 * @param {*Int} pageNum 
 * @param {*Function} callback 
 * 
 * 请求方式: GET
 * 请求参数: account-账号,若没有填写账号，则按时间顺序查询全部项目
 *          pageNum-页码
 * 返回参数: code-状态码,0代表成功
 *          msg-状态说明
 *          data-返回的数据项，list，包含以下参数
 *          id-项目ID
 *          account-项目上传者账号
 *          uploadDate-上传时间
 *          filename-项目名称
 *          href-项目链接
 */
function getOnesPro(account,pageNum,callback){
    if(callback === null && typeof callback !== 'function'){
        throw('getOnesPro: 参数错误')
    }
    let data;
    if(account){
        data = {
            account:account,
            currentPage:pageNum
        }
    }else{
        data = {
            currentPage:pageNum
        }
    }
    $snrj.ajax({
        url:SEARCHPRO,
        data:data,
        dataType:'json',
        method:'get',
        success:function(status,res,e){
            if(status.status === 200){
                // console.log(e);
                callback(res);
            }else{
                callback({code:2,msg:status.status + ' : ' + status.statusText});
            }
        },
        error:function(e){
            console.log(e);
        }
    })
}
/**
 * 删除项目接口，对应接口文档第五个接口
 * @param {*String} account 
 * @param {*String} id 
 * @param {*Function} callback 
 * 
 * 请求方式: POST
 * 请求参数: account-账号，id-项目ID
 * 返回参数: code-状态码,0代表成功
 *          msg-状态说明
 */
function deletePro(account,id,callback){
    $snrj.ajax({
        url:DELETE,
        data:{
            account:account,
            id:id
        },
        method:'post',
        dataType: 'json',
        success:function(status,res,e){
            if(status.status === 200){
                callback(res);
            }else{
                callback({code:2,msg:status.status + ' : ' + status.statusText});
            }
        }
    })
}

/**
 * 获取记录数接口，对应接口文档第七个接口
 * @param {*String} account 
 * @param {*String} sign 
 * @param {*Function} callback 
 * 
 * 请求方式: GET
 * 请求参数: sign-请求标识，0表示查询所有记录数，1表示查询某一账号的所有记录数
 *          account-账号，当sign=1时，必填
 * 返回参数: code-状态码,0代表成功
 *          msg-状态说明
 *          data-返回的数据项，object，包含以下参数
 *          recordCount-总记录数
 *          pageCount-总页码数
 */
function getRecordNum(account,sign,callback){
    $snrj.ajax({
        url:RECORDNUM,
        data:{
            account:account,
            sign:sign
        },
        method:'get',
        dataType:'json',
        success:function(status,res,e){
            if(status.status === 200){
                callback(res);
            }else{
                callback({code:2,msg:status.status + ' : ' + status.statusText});
            }
        }
    })
}


