<template>
    <div class="login">
        <div>
            <img src="../assets/logo.png">
        </div>
        <el-row>
            <el-col :xs="8" :sm="8" :md="9" :lg="9">&nbsp;</el-col>
            <el-col :xs="8" :sm="8" :md="6" :lg="6">
                <el-form :label-position="labelPosition" ref="loginForm" :rules="rules" label-width="80px" :model="loginInfo">
                    <el-form-item label="账号:" prop="account">
                        <el-input type="text" v-model="loginInfo.account"></el-input>
                    </el-form-item>
                    <el-form-item label="密码:" prop="passwd">
                        <el-input type="password" v-model="loginInfo.passwd"></el-input>
                    </el-form-item>
                </el-form>
                <el-button type="primary" @click="submitForm('loginForm')">登录</el-button>
                <el-button @click="resetForm('loginForm')">重置</el-button>
            </el-col>
            <el-col :xs="8" :sm="8" :md="9" :lg="9">&nbsp;</el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data() {
        let checkAccount = (rule, value, callback) => {
            if(!value){
                return callback(new Error('请输入账号'));
            }
            let isInvalid = this.$util.isInvalid(0, value);
            if (!isInvalid) {
                return callback(new Error('账号不正确'));
            } else {
                return callback();
            }
        };
        let checkPwd = (rule, value, callback) => {
            if(!value){
                return callback(new Error('请输入密码'));
            }
            let isInvalid = this.$util.isInvalid(1, value);
            if (!isInvalid) {
                return callback(new Error('密码格式不正确'));
            } else {
                return callback();
            }
        };
        return {
            labelPosition: 'top',
            loginInfo: {
                account: '',
                passwd: ''
            },
            rules: {
                account: { validator: checkAccount, required: true, trigger: 'blur' },
                passwd: { validator: checkPwd, required: true, trigger: 'blur' }
            }
        }
    },
    mounted(){
        this.$nextTick(()=>{
            if(this.isLogin){
                this.$router.push({path:'/myproject'});
            }
        })
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                let _this = this;
                if (valid) {
                    this.$util.login(this.loginInfo.account,this.loginInfo.passwd,(res)=>{
                        if(res.code === 0){
                            this.$store.commit('setUserInfo',_this.loginInfo);
                            this.$router.push({path:'/myproject'});
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    },
    computed:{
        isLogin(){
            return this.$store.state.isLogin;
        }
    }
}
</script>
<style>
.el-form {
    margin-top: 40px;
}
.el-form--label-top .el-form-item__label {
    float: left;
}
.el-form-item{
    text-align: left;
}
.login{
    text-align: center;
}
.login div img{
    background: #324157;
    border-radius: 40%;
    width: 100px;
    margin-top: 20px;
}
</style>



