<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <el-menu theme="dark" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1"><img class="logo" src="./assets/logo.png"> 前端页面展示</el-menu-item>
      <el-menu-item index="2" v-if="isLogin">我的项目</el-menu-item>
      <el-menu-item index="3">社区</el-menu-item>
      <el-menu-item index="4"><router-link to="info" target="_blank">使用说明</router-link></el-menu-item>
      <!-- <el-submenu index="5">
        <template slot="title">友情链接</template>
        <el-menu-item index="5-1"><a href="https://www.syau.edu.cn" target="_blank">神农软件首页</a></el-menu-item>
      </el-submenu> -->
      <el-menu-item index="6" v-if="isLogin">上传</el-menu-item>
      <el-menu-item index="7" class="user" v-if="isLogin">退出</el-menu-item>
      <el-menu-item index="8" class="user" v-if="isLogin">{{ getUserInfo.account }}</el-menu-item>
      <el-menu-item index="9" class="user" v-if="!isLogin">请登录</el-menu-item>
    </el-menu>
    <router-view></router-view>
    <div class="footer">
        <div>前端页面展示系统</div>
        <div>技术支持：神农大学生软件创新中心
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          开发人员：周梦凯、王兴龙、张雨航</div>
        <div>友情链接：
            <a href="http://www.syau.edu.cn">沈阳农业大学</a>
            &nbsp;|&nbsp;
            <a href="http://snsoft.syau.edu.cn">神农大学生软件创新中心</a>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      activeIndex: ''
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      // console.log(key, keyPath);
      switch(key){
        case '1':
          if(this.isLogin){
            this.$router.push({path:'/myproject'});
          }else{
            this.$router.push({path:'/'});
          }
          break;
        case '2':
          this.$router.push({path:'/myproject'});
          break;
        case '3':
          this.$router.push({path:'/project'});
          break;
        case '6':
          this.$router.push({path:'/upload'});
          break;
        case '7':
          this.$store.commit('clearUserInfo');
          this.$router.push({path:'/'});
          break;
        case '9':
          this.$router.push({path:'/'});
          break;
      }
    }
  },
  computed:{
    isLogin(){
      return this.$store.state.isLogin;
    },
    getUserInfo(){
      return this.$store.state.userInfo;
    }
  }
}
</script>

<style>
body{
  margin: 0 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
}
.el-menu-item a{
  text-decoration: none;
}
.logo{
  width: 40px;
  vertical-align: middle;
}
.user{
  float: right !important;
}
.footer{
  background: #324157;
  margin-top: 100px;
  padding: 20px 0;
  text-align: center;
  color: #efefef;
  font-size: 14px;
}
.footer div + div{
  margin-top: 10px;
}
.footer a{
  text-decoration: none;
  color: #efefef;
}
.footer a:hover{
  text-decoration:underline;
}
@media screen and (max-width: 850px){
  body{
    width: 850px;
  }
}
</style>



