'use strict';
export default {ajax}
/**
 * ajax
 * @param {*Object} options 
 * options {
 *      url:请求地址
 *      method:请求方法GET，POST
 *      data:请求参数，是一个Object
 *      dataType:返回数据类型
 *      async:异步
 *      timeout:请求超时
 *      progress:传输过程中触发的函数，可以制作进度条，必须为function
 *      success:传输成功时触发的函数，必须为function
 *      error:传输失败时触发的函数，必须为function，注意 http state 404 并不会触发该函数
 *      abort:传输断开时触发的函数，必须为function
 * }
 */
function ajax(options){
    if(typeof options !== 'object'){
        throw("参数数据类型错误，应使用Object");
        return {code:1,msg:"参数数据类型错误，应使用Object"};
    }
    let _options = {
        url:'',
        method:'GET',
        data:null,
        dataType: '',
        async: true,
        timeout: null,
        progress: function(data){},
        success: function(data){},
        error: function(data){},
        abort: function(data){},
        beforeSend: null,
        complete:null
    }

    function _extend(target,options){
        if(typeof target !== 'object' || typeof options !== 'object'){
            throw("参数数据类型错误，应使用Object");
            return false;
        }
        let name;
        for(name in options){
            if(options.hasOwnProperty(name) && !target.hasOwnProperty(name)){
                target[name] = options[name];
            }
        }
        return true;
    }

    function hasFile(dataObj){
        if(typeof dataObj !== 'object'){
            return false;
        }
        let key,type;
        for(key in dataObj){
            type = typeof dataObj[key];
            if(type === 'object'){
                if(dataObj[key].constructor === File){
                    return true;
                }
                return hasFile(dataObj[key]);
            }
        }
        return false;
    }

    function initPostData(reqParams){
        let typeReg = /(string|number|boolean|undefined|null)/i;
        let type = typeof reqParams;
        if(type === 'function'){
            throw('请求参数不能为function.')
        }
        if(typeReg.test(type)){
            return reqParams;
        }
        let data,key;
        if(hasFile(reqParams)){
            data = new FormData();
            for(key in reqParams){
                data.append(key,reqParams[key]);
            }
        }else{
            data = '';
            for(key in reqParams){
                data += '&' + key + '=' + reqParams[key];
            }
            data = data.substr(1);
        }
        return data;
    }
    function initGetData(reqParams){
        let typeReg = /(string|number|boolean|undefined|null)/i;
        let type = typeof reqParams;
        if(type === 'function'){
            throw('请求参数不能为function.')
        }
        if(typeReg.test(type)){
            return reqParams;
        }
        let data = '?', key;
        for(key in reqParams){
            data += key + '=' + reqParams[key] + '&';
        }
        data = data.replace(/&$/,'');
        return data;
    }

    function init(){
        if(!options || !options.url){
            throw('请正确填写参数');
        }
        if(!_extend(options, _options)){
            throw('请正确填写参数');
        }
        options.method = options.method.toUpperCase();
        let data;
        switch(options.method){
            case 'GET':
                options.url += initGetData(options.data);
                data = null;
                break;
            case 'POST':
                data = initPostData(options.data);
                break;
        }
        
        let xhr = new XMLHttpRequest();
        if(options.dataType){
            xhr.responseType = options.dataType;
        }
        xhr.upload.addEventListener('progress',function(data){
            if(typeof options.progress !== 'function'){
                throw('progress 参数必需为 函数 (function)');
            }
            let loaded = data.loaded;
            let total = data.total;
            let event = data;
            options.progress(loaded,total,event);
        }, false);
        xhr.addEventListener('load',function(data){
            if(typeof options.success !== 'function'){
                throw('load 参数必需为 函数 (function)');
            }
            let status = {
                status:data.target.status,
                statusText:data.target.statusText
            }
            let response = data.target.response;
            let event = data.target;
            options.success(status,response,event);
        },false);
        xhr.addEventListener('error',function(data){
            if(typeof options.error !== 'function'){
                throw('error 参数必需为 函数 (function)');
            }
            options.error(data);
        },false);
        xhr.addEventListener('abort',function(data){
            if(typeof options.abort !== 'function'){
                throw('abort 参数必需为 函数 (function)');
            }
            options.abort(data);
        },false);
        xhr.onreadystatechange = function () {
            switch (xhr.readyState) {
                case 1://OPENED
                    //do something
                    break;
                case 2://HEADERS_RECEIVED
                    //do something
                    break;
                case 3://LOADING
                    //do something
                    break;
                case 4://DONE
                    if(typeof options.complete === 'function'){
                        options.complete(xhr);
                    }
                    break;
            }
        }
        if(typeof options.beforeSend === 'function'){
            options.beforeSend(xhr);
        }
        xhr.open(options.method, options.url, options.async);
        try{
            xhr.send(data)
        }catch(e){
            console.log('a',e);
        };

        if(typeof options.timeout === 'number'){
            if(xhr.timeout){
                xhr.timeout = options.timeout;
            }else{
                setTimeout(() => {
                    xhr.abort();
                },options.timeout)
            }
        }
        return xhr;
    }

    return init();
}


