<template>
    <div class="introduction">
        <h2>使用说明</h2>
        <el-row>
            <el-col :xs="4" :sm="4" :md="5" :lg="5">&nbsp;</el-col>
            <el-col :xs="16" :sm="16" :md="14" :lg="14">
                <p>主要为了展示前端人员制作的界面</p>
                <div v-for="(item,index) in items" :key="index">
                    <h3>{{item.title}}</h3>
                    <img :src="item.img"/>
                    <p>{{ item.content }}</p>
                </div>
            </el-col>
            <el-col :xs="4" :sm="4" :md="5" :lg="5">&nbsp;</el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            items:[
                {
                    title:'步骤一',
                    img:require('../assets/1.png'),
                    content:"新建项目文件夹，项目文件夹名称最好为中文，如图中项目文件夹名称为 myfirstweb。项目文件夹中必须包含index.html。"
                },
                {
                    title:'步骤二',
                    img:require('../assets/2.png'),
                    content:"项目完成后，压缩项目文件夹为 zip 格式，其他格式无法上传。压缩包名称必须为英文"
                },
                {
                    title:'步骤三',
                    img:require('../assets/3.png'),
                    content:"登录本系统 (前端页面展示系统)。"
                },
                {
                    title:'步骤四',
                    img:require('../assets/4.png'),
                    content:"点击导航栏 上传 选项卡。填写项目名称，只允许使用字母、数字、下划线。选择步骤二对应的项目压缩包，必须为 zip 格式。"
                },
                {
                    title:'步骤五',
                    img:require('../assets/5.png'),
                    content:"点击导航栏 我的项目 选项卡。就可以看见刚才上传的项目。复制项目网址到浏览器地址栏进行访问。"
                },
                {
                    title:'步骤六',
                    img:require('../assets/6.png'),
                    content:"点击导航栏 社区 选项卡。也可以查看项目。"
                }
            ]
        }
    }
}
</script>
<style scoped>
.introduction h2{
    text-align: center;
}
img{
    width: 100%;
}
p{
    text-indent: 32px;
    font-size: 16px;
    line-height: 24px;
}
</style>



