<template>
  <div class="projectList">
    <el-row>
      <el-col :span="6">
        <el-menu default-active="0000" class="el-menu-vertical-demo" @select="handleSelect">
          <el-menu-item index="0000">最新上传</el-menu-item>
          <el-menu-item v-for="(value,index) in menuList" 
            :key="index" :index="value.account">
            {{value.username}}
          </el-menu-item>
        </el-menu>
      </el-col>
      <el-col :span="18">
        <el-table :data="tableData" border style="width:100%" max-height="250">
          <el-table-column prop="fileName" label="项目名称">
          </el-table-column>
          <el-table-column prop="href" label="项目地址">
          </el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <a :href="scope.row.href" target="_blank">查看</a>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="changePage"
          :current-page="currentPage"
          layout="total, prev, pager, next, jumper"
          :total="recordCount"
          :page-count="pageCount">
        </el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import mounted from '../js/projectlist/mounted'
import methods from '../js/projectlist/methods'
import data from '../js/projectlist/data'
import watch from '../js/projectlist/watch'
export default {
  data,
  watch,
  mounted,
  methods
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-tree {
  border: 0;
  border-right: 1px solid #d1dbe5;
}
.el-pagination{
  margin-top: 20px;
  text-align: center;
}
</style>



