export default {
    menuActive:{
        handler(val,oldVal){
            let account = val;
            let sign = 1;
            if(val === '0000'){
                account = null;
                sign = 0;
            }
            this.$util.getOnesPro(account,sign,(res)=>{
                if(res.code === 0){
                    this.tableData = res.data;
                }
            })
        }
    }
}


