<template>
    <div class="upload">
        
        <el-row>
            <el-col :xs="8" :sm="8" :md="8" :lg="8">&nbsp;</el-col>
            <el-col :xs="8" :sm="8" :md="6" :lg="6">
                <div>
                    <img src="../assets/upload.png">
                </div>
                <el-form :label-position="labelPosition" label-width="80px">
                    <el-form-item label="项目名称:">
                        <el-input v-model="fileName" placeholder="只允许字母、数字、下划线"></el-input>
                    </el-form-item>
                    <el-form-item label="文件:">
                        <a href="javascript:;" class="uploadFile">
                            <input type="file" @change="selectFile" ref="fileselector">
                            <span ref="selectInfo">点击选择zip文件</span>
                        </a>
                    </el-form-item>
                </el-form>
                <el-progress :percentage="percent" v-if="isShowPer"></el-progress>
                <el-button type="primary" @click="uploadFile">上传</el-button>
                <el-button @click="resetForm(0)">重置</el-button>
            </el-col>
            <el-col :xs="8" :sm="8" :md="10" :lg="10">&nbsp;</el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data() {
      return {
        labelPosition: 'left',
        fileName: '',
        file:'',
        percent:0,
        isShowPer:false
      };
    },
    mounted(){
        this.$nextTick(()=>{
            if(!this.isLogin){
                this.$router.push({path:'/'});
            }
        })
    },
    methods:{
        uploadFile(){
            if(!this.$util.isInvalid(2,this.file.name)){
                this.$message({
                    type:'error',
                    message:'请上传zip压缩格式文件。'
                })
                this.resetForm(1);
                return;
            }
            if(!this.$util.isInvalid(3,this.fileName)){
                this.$message({
                    type:'error',
                    message: '请填写正确的文件名'
                })
                return;
            }
            this.isShowPer=true;
            this.$util.upload(this.fileName,this.file,(loaded,total,data)=>{
                console.log('progress',loaded,total,data);
                this.percent = parseInt(loaded/total) * 100;
            },(res)=>{
                this.isShowPer=false;
                if(res.code === 0){
                    this.$message({
                        type:'success',
                        message:'上传成功'
                    })
                }else{
                    this.$message({
                        type:'error',
                        message:res.msg
                    })
                }
            })
        },
        selectFile(){
            // console.log(this.$refs.fileselector.files)
            let files = this.$refs.fileselector.files;
            if(files.length === 0){
                this.resetForm(1);
            }else{
                if(!this.$util.isInvalid(2,files[0].name)){
                    this.$message({
                        type:'error',
                        message:'请上传zip或rar压缩格式文件。'
                    })
                    this.resetForm(1);
                    return;
                }
                if(!this.$util.isInvalid(4,files[0].size)){
                    this.$message({
                        type:'error',
                        message:'请上传大小不超过5M的文件'
                    })
                    this.resetForm(1);
                    return;
                }
                this.file = files[0];
                let info = this.$refs.selectInfo;
                console.log(this.file)
                info.innerText = this.file.name;
                info.style.color = '#1f2d3d';
            }
        },
        resetForm(type){
            if(type === 0){
                this.fileName = '';
            }
            this.file = null;
            let info = this.$refs.selectInfo;
            // console.log(this.file)
            info.innerText = '点击选择zip文件';
            info.style.color = '#bfcbd9';
        }
    },
    computed:{
        isLogin(){
            return this.$store.state.isLogin;
        }
    }
  }
</script>
<style>
.el-form{
    margin-top: 40px;
}
.el-form--label-top .el-form-item__label{
    float: left;
}
.el-progress{
    margin-bottom: 22px;
}
.upload{
    text-align: center;
}
.upload img{
    width: 100px;
    margin-top: 20px;
}
.uploadFile{
    width:100%;
    height: 36px;
    padding: 3px 10px;
    box-sizing: border-box;
    display: inline-block;
    position: relative;
    background: #fff;
    border: 1px solid #bfcbd9;
    border-radius: 4px;
    text-decoration: none;
    color: #1f2d3d;
    line-height: 28px;
    font-size: 14px;
    text-align: left;
    overflow: hidden;
}
.uploadFile input{
    width: 100%;
    height: 28px;
    opacity: 0; 
    position: absolute;
    top:3px;
    left:0;
}
.uploadFile span{
    color:#bfcbd9;
}
</style>



