export default {
  handleSelect(key, keyPath) {
    // console.log(key, keyPath);
    this.menuActive = key;
    let sign = 1;
    if(key == '0000'){
      key = null;
      sign = 0;
    }
    this.$util.getRecordNum(key, sign, (res) => {
      if (res.code === 0) {
        this.recordCount = res.data.recordCount;
        this.pageCount = res.data.pageCount;
        this.tableData = [];
        if(this.recordCount != 0){
          this.$util.getOnesPro(key, 1, (res) => {
            if (res.code === 0) {
              this.tableData = res.data;
            }
          })
        }
      }
    })
  },
  changePage(val){
    // console.log(this.menuActive,val)
    // this.$util.getOnesPro()
    let account = this.menuActive;
    if(account === '0000'){
      account = null;
    }
    this.$util.getOnesPro(account, val, (res) => {
      if (res.code === 0) {
        this.tableData = res.data;
      }
    })
  }
}



